body {
    height: 100vh;
    width: 100%;
    margin: 0;
    background: rgb(255,207,228);
    background: linear-gradient(90deg, rgb(255, 167, 205) 0%, rgb(161, 197, 241) 100%);
    -webkit-animation: gradientBG 10s ease infinite;
            animation: gradientBG 10s ease infinite;
    background-size: 500% 400%;
}

@-webkit-keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

.token {
    width: 300px;
    box-shadow: 0 8px 16px rgba(5,5,5,0.3);
    padding: 2px 16px;
    margin: 16px;
    border-radius: 8px;
    /* background: url(); */
}

img {
    border: .3px rgba(#e0e0e0);
    box-shadow: 10px 10px 8px rgba(#111111);
    border-radius: 4px;
    padding: 5px;
    width: 150px;
    margin-top: 10px;
}

.container-filled {
    position: absolute;
    width: 100%;
    text-align: center;
    top: 15%;
}
